import axios from "axios";

export default async function WatchMedia({ params }) {
  // Await params before destructuring (Next.js 15 requirement)
  const { id, mediaType } = await params;
  
  try {
    // Build embed URL exactly like your pattern
    const embedUrl = mediaType === 'movie' 
      ? `${process.env.NEXT_PUBLIC_VIDEO_API_BASE}/movie/${id}`
      : `${process.env.NEXT_PUBLIC_VIDEO_EMBED_1}/tv/${id}`;
      
    const API_KEY = process.env.NEXT_PUBLIC_TMDB_API_KEY;
    
    const response = await axios.get(
      `https://api.themoviedb.org/3/${mediaType}/${id}?api_key=${API_KEY}`
    );
    
    const media = response.data;
    
    return (
      <div className="min-h-screen bg-black text-white">
        <div className="p-4">
          <h1 className="text-2xl font-bold mb-4">{media.title || media.name}</h1>
          <div className="w-full h-[80vh]">
            <iframe 
              src={embedUrl}
              className="w-full h-full rounded-lg"
              allowFullScreen
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            ></iframe>
          </div>
        </div>
      </div>
    );
    
  } catch (error) {
    return (
      <div className="min-h-screen bg-black text-white flex items-center justify-center">
        <div className="text-center">
          <h1 className="text-2xl font-bold mb-4">Error Loading Video</h1>
          <p>Could not load the requested content.</p>
        </div>
      </div>
    );
  }
}
